.modal {
    position: fixed;
    height: 100vh;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
}

.product {
    position: relative;
}

.filter_responsive_btn {
    margin-top: 0px;
    padding: 8px 13px;
    font-size: 1.5rem;
    border: 1px solid #333;
    border-radius: 10rem;
    cursor: pointer;
    margin-left: 1.5rem;
    position: absolute;
    top: 20px;
    background-color: white;
    display: none;
}

.filter_responsive_btn_fixed {
    position: fixed;
    top: 100px;
}

.filter_responsive_btn>p {
    display: inline;
}

@media screen and (max-width: 1000px) {
    .filter_responsive_btn {
        display: 'inline';
    }
}