.filter {
    top: 0;
    margin-left: 3rem;
    width: 11rem;
    position: relative;
    scrollbar-width: thin;
    scrollbar-color: rgb(109, 109, 109) rgb(255, 255, 255);
    overflow-y: scroll;
    height: calc(100vh - 80px);
    z-index: 10;
    background-color: white;
}

.filter::-webkit-scrollbar {
    width: 5px;
}

.filter::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
.filter::-webkit-scrollbar-thumb {
    background: #888;
}

.filter__line {
    display: flex;
    flex-direction: column;
}

.filter__line>p {
    font-weight: bolder;
    margin: 0.8rem 0;
}

.filter__line>div {
    height: 30px;
}

.category {
    width: 10rem;
    margin-top: 0.8rem;
    flex-direction: column;
    font-weight: bold;
}

.category>div {
    display: inline-block;
    padding: 5px;
    cursor: pointer;
    text-align: left;
}

.checkbox {
    height: 20px;
}

.close-btn {
    display: none;
}

.filter_fixed {
    height: calc(100vh - 75px);
    top: 68px;
    position: fixed;

}

@media screen and (max-width: 1000px) {
    .filter {
        width: 100vw;
        height: 100vh;
        font-size: 1.5rem;
        position: fixed;
        top: 0%;
        left: 0%;
        transition: all 0.8s;
        background-color: #fff;
        z-index: 30;
        min-height: 100vh;
        margin: 0;
        display: flex;
        flex-direction: column;
        overflow: scroll;
        transform: translateY(100vh);
    }

    .first__filter__line {
        margin-top: 100px;
    }

    .filter__line {
        margin-left: 50px;
        border-bottom: 1px solid rgb(219, 219, 219);
        padding-bottom: 20px;
        width: calc(100% - 100px);
        font-size: 2rem;

    }

    .filter__line input {
        height: 2rem;
        width: 2rem;

    }

    .filter__line>div {
        padding-bottom: 20px;
    }

    .close-btn {
        display: block;
        position: absolute;
        top: 30px;
        right: 30px;
        cursor: pointer;
        background-color: black;
        color: #fff;
        padding: 20px;
        border-radius: 40%;
    }
}