.detailPage {
    margin-top: 0;
    display: flex;
    flex-direction: row;
    height: 100vh;
    overflow: hidden;
}

.mainImg_container {
    flex-direction: column;
    align-items: center;
    flex: 1;
    margin-left: 30px;
}

.mainImg_container img {
    height: 100vh;
}

.product_detail {
    background-color: #fff;
    flex: 0.8;
    margin: 0;
    padding-top: 100px;
    z-index: 5;
    padding-left: 50px;
}

.product_detail p {
    width: 80%;
    margin: 5px 0;
}

.imgList_container {
    height: 80px;
    margin: 10px 0;
}

.imgList_container img {
    margin-right: 10px;
    border: 1px solid transparent;
    cursor: pointer;
}

.imgList button {
    width: 80%;
    padding: 15px 0;
    font-size: 1rem;
    margin: 10px 0;
    ;
}

.Detail__btn--addToCart {
    border: 1px solid #03a9f4 !important;
    color: #03a9f4 !important;
}

.Detail__btn--like {
    background-color: #03a9f4 !important;
    color: white !important;
}

@media screen and (max-width: 1000px) {
    .detailPage {
        flex-direction: column;
        font-size: 10rem;
    }

    .mainImg_container {
        margin-left: 0;
        height: auto;
    }

    .mainImg_container img {
        height: 50vh;
    }

    .product_detail {
        margin: 0 76px;
        font-size: 1.8rem;
        padding: 0;
    }

    .imgList_container {
        height: 150px;
        margin: 30px 0;
    }

    .imgList_container img {
        margin-right: 20px;
    }

    .imgList button {
        font-size: 2rem;
        height: 5rem;
        padding: 40px 0;
        padding-bottom: 80px;
        width: 100%;
    }

    .product_detail_description {
        margin-bottom: 30px;
        font-size: 2rem;
    }

    .product_detail p {
        width: 100%;
    }
}